<script>
export default {
  name: "PageContent",
  components: {
      
  },
  data() {
      return {
          subMenuVisible: false,
          showHeader: true,
          aboutPage: false
      }
  },
  props: {
    msg: String
  }, 
  methods: {
      route: function() {

        if(this.$router.currentRoute.name == 'Home') {
            this.showHeader = false
        }

        if(this.$router.currentRoute.name == 'About') {
            this.aboutPage = true
            return 'about'
        } else if(this.$router.currentRoute.name == 'People') {
            return 'people'
        } else if(this.$router.currentRoute.name == 'PressAwards') {
            return 'press'
        } else if(this.$router.currentRoute.name == 'Maps') {
            return 'maps'
        } else if(this.$router.currentRoute.name == 'Home') {
            return 'home'
        }
      }
  },
  created() {
      this.route()
  }
};
